import { useIncidents } from '@/hooks/useIncidents';
import { useServices } from '@/hooks/useServices';
import { useUsers } from '@/hooks/useUsers';
import StatCard from '@/components/admin/StatCard';
import { Skeleton } from '@/components/ui/skeleton';
import { AlertTriangle, ShieldAlert, Users } from 'lucide-react';
import { motion } from 'framer-motion';
import RecentIncidents from '@/components/admin/dashboard/RecentIncidents';
import ServicesOverview from '@/components/admin/dashboard/ServicesOverview';
import QuickActions from '@/components/admin/dashboard/QuickActions';
import WebhookInstructions from '@/components/admin/WebhookInstructions';
import RecentActivity from '@/components/admin/dashboard/RecentActivity';
import {
  Accordion,
  AccordionContent,
  AccordionItem,
  AccordionTrigger,
} from '@/components/ui/accordion';

const AdminDashboard = () => {
  const { activeIncidents, loading: incidentsLoading } = useIncidents();
  const {
    problematicServices,
    loading: servicesLoading,
  } = useServices();
  const { totalUsers, loading: usersLoading } = useUsers();

  const loading = incidentsLoading || servicesLoading || usersLoading;

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1,
        delayChildren: 0.2,
      },
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 20, scale: 0.95 },
    visible: {
      opacity: 1,
      y: 0,
      scale: 1,
      transition: {
        duration: 0.5,
        ease: [0.25, 0.46, 0.45, 0.94],
      },
    },
  };

  const slideInFromLeft = {
    hidden: { opacity: 0, x: -20 },
    visible: {
      opacity: 1,
      x: 0,
      transition: {
        duration: 0.5,
        ease: [0.25, 0.46, 0.45, 0.94],
      },
    },
  };

  return (
    <motion.div
      variants={containerVariants}
      initial="hidden"
      animate="visible"
      className="space-y-8"
    >
      {/* Header Section */}
      <motion.div variants={itemVariants} className="space-y-2">
        <h1 className="text-4xl font-bold bg-gradient-to-r from-foreground via-foreground/90 to-foreground/70 bg-clip-text text-transparent">
          Tableau de Bord
        </h1>
        <p className="text-lg text-muted-foreground">
          Vue d'ensemble de l'infrastructure et des services.
        </p>
      </motion.div>

      {/* Main Grid */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
        {/* Main Content (Left) */}
        <div className="lg:col-span-2 space-y-8">
          {/* Stats Grid */}
          <motion.div
            variants={itemVariants}
            className="grid gap-6 md:grid-cols-2 lg:grid-cols-3"
          >
            {loading ? (
              <>
                <Skeleton className="h-[140px] rounded-xl" />
                <Skeleton className="h-[140px] rounded-xl" />
                <Skeleton className="h-[140px] rounded-xl" />
              </>
            ) : (
              <>
                <StatCard
                  title="Incidents Actifs"
                  value={activeIncidents.length.toString()}
                  icon={AlertTriangle}
                  variant={activeIncidents.length > 0 ? 'danger' : 'success'}
                />
                <StatCard
                  title="Services Problématiques"
                  value={problematicServices.length.toString()}
                  icon={ShieldAlert}
                  variant={
                    problematicServices.length > 0 ? 'warning' : 'success'
                  }
                />
                <StatCard
                  title="Utilisateurs"
                  value={totalUsers.toString()}
                  icon={Users}
                  variant="default"
                />
              </>
            )}
          </motion.div>

          <motion.div variants={slideInFromLeft}>
            <ServicesOverview />
          </motion.div>
          <motion.div variants={slideInFromLeft}>
            <RecentIncidents />
          </motion.div>
        </div>

        {/* Sidebar (Right) */}
        <div className="lg:col-span-1 space-y-8">
          <motion.div variants={itemVariants}>
            <QuickActions />
          </motion.div>
          <motion.div variants={itemVariants}>
            <RecentActivity />
          </motion.div>
          <motion.div variants={itemVariants}>
            <Accordion type="single" collapsible>
              <AccordionItem value="webhooks">
                <AccordionTrigger>
                  Instructions pour les Webhooks
                </AccordionTrigger>
                <AccordionContent>
                  <WebhookInstructions />
                </AccordionContent>
              </AccordionItem>
            </Accordion>
          </motion.div>
        </div>
      </div>
    </motion.div>
  );
};

export default AdminDashboard;