import { useAuditLogs, AuditLog } from '@/hooks/useAuditLogs';
import { useTranslation } from 'react-i18next';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Skeleton } from '@/components/ui/skeleton';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { getGravatarURL } from '@/lib/gravatar';
import { formatDistanceToNow } from 'date-fns';
import { fr, enUS } from 'date-fns/locale';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Badge } from '@/components/ui/badge';
import { GitCommit } from 'lucide-react';
import { motion } from 'framer-motion';

const actionColors: { [key: string]: string } = {
  user_login: 'bg-green-500/20 text-green-400 border-green-500/30',
  user_logout: 'bg-gray-500/20 text-gray-400 border-gray-500/30',
  user_deleted: 'bg-red-500/20 text-red-400 border-red-500/30',
  user_created_by_admin: 'bg-blue-500/20 text-blue-400 border-blue-500/30',
  admin_mfa_disabled: 'bg-yellow-500/20 text-yellow-400 border-yellow-500/30',
  default: 'bg-muted text-muted-foreground border-border',
};

const LogItem = ({ log, locale }: { log: AuditLog, locale: any }) => {
  const { t } = useTranslation();
  const userEmail = log.profiles?.email || 'System';
  const userName = log.profiles?.first_name || 'System';
  const actionText = t(`audit_logs.${log.action}`, { ...log.details, defaultValue: log.action });

  return (
    <div className="flex items-start gap-4 p-3 hover:bg-muted/50 rounded-lg transition-colors">
      <Avatar className="h-9 w-9">
        <AvatarImage src={getGravatarURL(userEmail)} />
        <AvatarFallback>{userName.charAt(0)}</AvatarFallback>
      </Avatar>
      <div className="flex-1">
        <p className="text-sm">
          <span className="font-semibold">{userName}</span> {actionText}
        </p>
        <p className="text-xs text-muted-foreground">
          {formatDistanceToNow(new Date(log.created_at), { addSuffix: true, locale })}
        </p>
        <Badge variant="outline" className={`mt-2 text-xs ${actionColors[log.action] || actionColors.default}`}>
          {log.action}
        </Badge>
      </div>
    </div>
  );
};

const RecentActivity = () => {
  const { t, i18n } = useTranslation();
  const { logs, loading } = useAuditLogs(1, 20);
  const currentLocale = i18n.language === 'fr' ? fr : enUS;

  return (
    <motion.div
      initial={{ opacity: 0, x: 20 }}
      animate={{ opacity: 1, x: 0 }}
      transition={{ duration: 0.5, delay: 0.4 }}
    >
      <Card className="h-full">
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <GitCommit className="h-5 w-5" />
            {t('recent_activity')}
          </CardTitle>
        </CardHeader>
        <CardContent>
          {loading ? (
            <div className="space-y-4">
              {[...Array(5)].map((_, i) => <Skeleton key={i} className="h-16 w-full" />)}
            </div>
          ) : (
            <ScrollArea className="h-[400px] pr-4">
              <div className="space-y-2">
                {logs.map(log => <LogItem key={log.id} log={log} locale={currentLocale} />)}
              </div>
            </ScrollArea>
          )}
        </CardContent>
      </Card>
    </motion.div>
  );
};

export default RecentActivity;
